/**
 * Mixins
 */
@keyframes cs-loader {
  0% {
    opacity: 0;
    transform: rotate(0); }
  33% {
    opacity: 1;
    transform: rotate(180deg); }
  66% {
    opacity: 1;
    transform: rotate(180deg); }
  100% {
    opacity: 0;
    transform: rotate(360deg); } }

@keyframes cs-loader-rtl {
  0% {
    opacity: 0;
    transform: rotate(360deg); }
  33% {
    opacity: 1;
    transform: rotate(180deg); }
  66% {
    opacity: 1;
    transform: rotate(180deg); }
  100% {
    opacity: 0;
    transform: rotate(0); } }

.cs-loader {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%; }

.cs-loader-inner {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1; }

.cs-loader-inner > div {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  width: 6px;
  height: 6px;
  border-radius: 6px;
  background-color: var(--color-foreground-default);
  margin-left: auto;
  margin-right: auto; }

.cs-loader .cs-loader-inner:nth-child(1) {
  animation: cs-loader 3s 1000ms infinite ease-in-out;
  padding-left: 0px; }
  :root[dir='rtl'] .cs-loader .cs-loader-inner:nth-child(1) {
    animation: cs-loader-rtl 3s 1000ms infinite ease-in-out;
    padding: 0 0px 0 0; }

.cs-loader .cs-loader-inner:nth-child(2) {
  animation: cs-loader 3s 800ms infinite ease-in-out;
  padding-left: 18px; }
  :root[dir='rtl'] .cs-loader .cs-loader-inner:nth-child(2) {
    animation: cs-loader-rtl 3s 800ms infinite ease-in-out;
    padding: 0 18px 0 0; }

.cs-loader .cs-loader-inner:nth-child(3) {
  animation: cs-loader 3s 600ms infinite ease-in-out;
  padding-left: 36px; }
  :root[dir='rtl'] .cs-loader .cs-loader-inner:nth-child(3) {
    animation: cs-loader-rtl 3s 600ms infinite ease-in-out;
    padding: 0 36px 0 0; }

.cs-loader .cs-loader-inner:nth-child(4) {
  animation: cs-loader 3s 400ms infinite ease-in-out;
  padding-left: 54px; }
  :root[dir='rtl'] .cs-loader .cs-loader-inner:nth-child(4) {
    animation: cs-loader-rtl 3s 400ms infinite ease-in-out;
    padding: 0 54px 0 0; }

.cs-loader .cs-loader-inner:nth-child(5) {
  animation: cs-loader 3s 200ms infinite ease-in-out;
  padding-left: 72px; }
  :root[dir='rtl'] .cs-loader .cs-loader-inner:nth-child(5) {
    animation: cs-loader-rtl 3s 200ms infinite ease-in-out;
    padding: 0 72px 0 0; }

.cs-loader .cs-loader-inner:nth-child(6) {
  animation: cs-loader 3s 0ms infinite ease-in-out;
  padding-left: 90px; }
  :root[dir='rtl'] .cs-loader .cs-loader-inner:nth-child(6) {
    animation: cs-loader-rtl 3s 0ms infinite ease-in-out;
    padding: 0 90px 0 0; }
