/**
 * General constants
 */
.circle {
  border: 1px solid var(--color-foreground-secondary);
  border-radius: 100%;
  -webkit-clip-path: circle(100% at center);
  clip-path: circle(100% at center);
  position: relative;
  overflow: hidden;
  flex-grow: 0;
  flex-shrink: 0;
  background-color: var(--color-foreground-inactive); }
  .circle :global(.icon) {
    color: var(--color-content-background-primary);
    text-align: center;
    display: block;
    width: 100%; }
  .circle.preview {
    width: 160px;
    height: 160px; }
    .circle.preview :global(.icon) {
      line-height: 160px;
      font-size: 80px; }
    .circle.preview img {
      width: 160px;
      height: 160px; }
  .circle.masthead {
    width: 36px;
    height: 36px; }
    .circle.masthead :global(.icon) {
      line-height: 36px;
      font-size: var(--font-size-h2); }
    .circle.masthead img {
      width: 36px;
      height: 36px; }
  .circle.list-item {
    width: 30px;
    height: 30px; }
    .circle.list-item :global(.icon) {
      line-height: 30px;
      font-size: 16px; }
    .circle.list-item img {
      width: 30px;
      height: 30px; }
  .circle.search-result {
    width: 64px;
    height: 64px; }
    .circle.search-result :global(.icon) {
      line-height: 64px;
      font-size: 28px; }
    .circle.search-result img {
      width: 64px;
      height: 64px; }
  .circle.list-tile {
    width: 50px;
    height: 50px; }
    .circle.list-tile :global(.icon) {
      line-height: 50px;
      font-size: 20px; }
    .circle.list-tile img {
      width: 50px;
      height: 50px; }
  .circle img {
    position: absolute;
    top: 0;
    left: 0; }
