/**
 * Mixins
 */
:global(.gallery-card-link) {
  display: inline-block;
  margin: 0.625rem;
  cursor: pointer; }
  :global(.gallery-card-link):not(:global(.disabled)):global(.selected) .card {
    box-shadow: var(--depth-3);
    border: 3px solid var(--color-accent); }
  :global(.gallery-card-link):not(:global(.disabled)):not(:global(.selected)):active .card {
    border: 1px solid var(--color-accent-selected);
    box-shadow: var(--depth-4); }
  :global(.gallery-card-link):not(:global(.disabled)):not(:global(.selected)):hover .card, :global(.gallery-card-link):not(:global(.disabled)):not(:global(.selected)):focus .card {
    border: 1px solid var(--color-accent);
    box-shadow: var(--depth-4); }

.card {
  overflow: hidden;
  position: relative;
  background-color: var(--color-content-background-primary);
  color: var(--color-foreground-default);
  border: 1px solid var(--color-foreground-secondary);
  box-shadow: var(--depth-1);
  border-radius: 2px; }
  .card.fixed {
    height: 17.75rem;
    width: 16.75rem; }

.background-image-container, .background-color {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.2s ease-in-out;
  border-bottom: 1px solid var(--color-foreground-secondary); }
  .fixed.background-image-container, .fixed.background-color {
    width: calc(16.75rem - 2px);
    height: calc(10rem - 2px); }
  .fullbg .background-image-container, .fullbg .background-color {
    height: 100%; }

.background-image-container {
  overflow: hidden; }
  .background-image-container .background-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center; }

.background-color {
  background-color: var(--color-content-background-primary); }
  .background-color.blue {
    background-color: var(--color-accent-selected); }
    .background-color.blue span {
      color: var(--color-accent-foreground); }

.card-content {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 1rem 1.25rem;
  font-family: var(--font-family-default);
  overflow: hidden; }
  .card-content header {
    font-size: var(--font-size-h4);
    line-height: var(--line-height-h4);
    font-weight: var(--font-weight-headings);
    margin-bottom: 0.25rem;
    flex: 0 0 1.25rem; }
  .card-content section {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-size: var(--font-size-small);
    line-height: 1rem;
    overflow: hidden; }
  .card-content footer {
    font-size: var(--font-size-default);
    line-height: 1.25rem;
    flex: 0 0 1rem; }
  .fixed .card-content {
    height: 7.75rem; }

.banner {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  padding: 0.25rem;
  background-color: var(--color-foreground-default);
  color: var(--color-foreground-complementary);
  font-family: var(--font-family-default);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center; }
