.alert-container {
  position: relative;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  width: 100%;
  height: 2rem;
  border-radius: 2px; }
  .alert-container .alert-icon {
    margin: 0.25rem 0.5rem;
    flex: 0 0 1rem; }
  .alert-container .alert-text {
    margin: 0.3125rem 0.75rem 0 0;
    padding-bottom: 0.5rem;
    color: inherit;
    font-family: var(--font-family-light);
    font-size: var(--font-size-default);
    line-height: 1.25rem;
    width: 100%;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
    :root[dir='rtl'] .alert-container .alert-text {
      margin: 0.3125rem 0 0 0.75rem; }
    .alert-container .alert-text + .close {
      margin-left: 0.5rem; }
      :root[dir='rtl'] .alert-container .alert-text + .close {
        margin: 0 0.5rem 0 0; }
  .alert-container .close-button:hover, .alert-container .close-button:focus {
    background-color: transparent; }
    .alert-container .close-button:hover .close-button-container > span, .alert-container .close-button:focus .close-button-container > span {
      font-weight: bold; }
  .alert-container .close-button:focus {
    outline-offset: -1px; }
  .alert-container .close-button .close-button-container > span {
    color: inherit; }
  .alert-container.alert-fixed {
    width: 17.75rem; }
  .alert-container.alert-info {
    background-color: var(--color-global-background-informational);
    color: var(--color-global-foreground-informational); }
  .alert-container.alert-warning {
    background-color: var(--color-global-background-warning);
    color: var(--color-global-foreground-warning); }
  .alert-container.alert-error {
    background-color: var(--color-global-background-error);
    color: var(--color-global-foreground-error); }
  .alert-container.alert-success {
    background-color: var(--color-global-background-success);
    color: var(--color-global-foreground-success); }
  .alert-container.alert-multiline {
    height: auto; }
    .alert-container.alert-multiline .alert-text {
      text-overflow: none;
      line-height: 1.25rem;
      white-space: normal; }
